body {
    background: #fff;
}

.business-page-detail {
    position: relative;
    top: -5rem;
    z-index: 10;
}

.business-contact {
    align-items: center;
    justify-content: space-between;
}
.tel {
    color: #fff;
    font-size: 1.2rem;
}
.buttn {
    width: 200px;
    height: 50px;
    align-items: center;
    justify-content: center;
    border-radius: 4px;
    &-success {
        background: #13DF80;
        color: #fff;
        font-size: 1.2rem;
    }
}
#header {
    background-size: cover;
    background-position: center center;
}
