/* tooltips */
.tooltip {
  position: relative; }
  .tooltip:before {
    content: attr(data-tooltip);
    position: absolute;
    box-sizing: border-box;
    max-width: 160px;
    background: rgba(97, 97, 97, 0.9);
    padding: 6px 16px;
    border-radius: 2px;
    color: #FFF;
    font-size: 14px;
    font-weight: normal;
    text-align: center;
    text-transform: none;
    line-height: 20px;
    white-space: normal;
    opacity: 0;
    transition: opacity .2s;
    bottom: calc(100% + 16px);
    left: 50%;
    pointer-events: none;
    transform: translate(-50%, 0); }
  .tooltip.tooltip-expanded:before {
    opacity: 1; }
  .tooltip.tooltip-top-left:before {
    left: 0;
    transform: translate(0%, 0); }
  .tooltip.tooltip-top-right:before {
    left: auto;
    right: 0;
    transform: translate(0%, 0); }
  .tooltip.tooltip-bottom-left:before {
    bottom: auto;
