#addReviewForm {
    background: #fff;
    width: 100%;
    max-width: 50rem;
    margin: 0 auto;
    position: absolute;
    left: 0px;
    right: 0px;
    top: 0px;
    z-index: 1000;
    padding: 3rem;
    .form-item { position:relative; }
    select,input {
        border:  2px solid lightgrey;
        border-radius: 5px; 
        min-height: 2rem; 
        width: 100%;
        max-width: initial;
    }
}
.closeFormBtn {
    float: right;
    position: relative;
    top: -2rem;
}
.c-form-title {
    font-size: 2.5rem;
    text-align: center;
    span {
        color: lightgreen;
    }
}

